.App {
  text-align: center;
}

body, html, #root {
  font-family: 'Poppins', Arial, Helvetica, sans-serif !important;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}


.scroll-top-btn {
  position: fixed;
  right: 32px;
  bottom: 32px;
  width: 48px;
  height: 48px;
  background: linear-gradient(135deg, #009961 70%, #00c47a 100%);
  color: #fff;
  border: none;
  border-radius: 50%;
  box-shadow: 0 4px 16px #00996133;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 2rem;
  z-index: 9999;
  cursor: pointer;
  transition: background 0.2s, box-shadow 0.2s, opacity 0.2s;
  opacity: 0.92;
}

.scroll-top-btn:hover {
  background: linear-gradient(135deg, #00c47a 70%, #009961 100%);
  box-shadow: 0 8px 24px #00996155;
  opacity: 1;
}

@media (max-width: 600px) {
  .scroll-top-btn {
    right: 16px;
    bottom: 16px;
    width: 40px;
    height: 40px;
    font-size: 1.5rem;
  }
}

.work-history .timeline {
  position: relative;
  margin: 2rem 0;
  padding-left: 30px;
}

.work-history .timeline-item {
  position: relative;
  margin-bottom: 2.5rem;
}

.work-history .timeline-content ul {
  list-style: none;
  padding-left: 0;
}

.work-history .timeline-marker {
  position: absolute;
  left: -8px;
  top: 1px;
  width: 20px;
  height: 20px;
  background: #fff;
  border: 3px solid #00c47a;
  /* accent green */
  box-shadow: 0 0 0 4px #e6fff4;
  border-radius: 50%;
  z-index: 1;
  transition: border-color 0.3s, box-shadow 0.3s;
}

.work-history .timeline-item:hover .timeline-marker {
  border-color: #009961;
  box-shadow: 0 0 0 8px #b6f2d5;
}

.work-history .timeline-content {
  background: #f8fefb;
  padding: 1.2rem 1.5rem;
  border-radius: 8px;
  box-shadow: 0 2px 8px #00996111;
  border-left: 4px solid #00c47a;
  transition: border-color 0.3s;
}

.work-history .timeline-item:hover .timeline-content {
  border-left: 4px solid #009961;
  box-shadow: 0 4px 16px #00996122;
}

.work-history .timeline-date {
  display: block;
  font-size: 0.95rem;
  color: #00c47a;
  margin-bottom: 0.5rem;
  font-weight: 600;
}

.work-history h4 {
  margin-bottom: 0.2rem;
  color: #009961;
  font-weight: 700;
  letter-spacing: 0.5px;
}

.work-history h5 {
  margin-bottom: 0.7rem;
  font-size: 1.1rem;
  color: #00c47a;
  font-weight: 600;
}

.work-history ul {
  margin: 0;
  padding-left: 1.2rem;
}

.work-history .timeline-content li {
  color: #222;
  font-size: 1rem;
  margin-bottom: 0.3em;
  position: relative;
  padding-left: 18px;
}

.work-history .timeline-content li::before {
  content: '';
  position: absolute;
  left: 0;
  top: 10px;
  width: 8px;
  height: 8px;
  background: linear-gradient(135deg, #009961 60%, #00c47a 100%);
  border-radius: 50%;
  box-shadow: 0 0 4px #00996144;
}

.work-history ul {
  margin: 0;
  padding-left: 1.2rem;
}

.skills-grid {
  display: flex;
  flex-wrap: wrap;
  gap: 16px;
  margin-top: 1rem;
}

.skill-chip {
  background: #f4f6fa;
  border-radius: 24px;
  padding: 0.75rem 1.5rem;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.04);
  font-size: 1rem;
  min-width: 180px;
  max-width: 260px;
  transition: box-shadow 0.2s;
  border: 1px solid #e3e7ed;
}

.skill-chip:hover {
  box-shadow: 0 4px 16px rgba(13, 110, 253, 0.08);
  border-color: #0d6efd;
}

.skill-chip span {
  font-weight: 600;
  color: #222;
}

.skill-chip small {
  color: #0d6efd;
  font-size: 0.95em;
  margin-top: 2px;
}

.skill-sub {
  margin-top: 6px;
  display: flex;
  flex-wrap: wrap;
  gap: 6px;
}

.skill-sub span {
  background: #e9f2ff;
  color: rgb(79, 102, 136);
  border-radius: 12px;
  padding: 2px 10px;
  font-size: 0.85em;
  font-weight: 500;
}


.skills-tree>ul {
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  gap: 40px;
  padding-left: 0;
  list-style: none;
  margin: 0;
  justify-content: flex-start;
  align-items: stretch;
}

.skills-tree>ul>li {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  background: #fff;
  border-radius: 10px;
  box-shadow: 0 4px 24px 0 #00996114, 0 1.5px 4px 0 rgba(0, 0, 0, 0.04);
  padding: 24px 20px 20px 20px;
  flex: 1 1 calc(50% - 20px);
  max-width: calc(50% - 20px);
  position: relative;
  transition: transform 0.25s cubic-bezier(.4, 2, .6, 1), box-shadow 0.2s;

  animation: fadeInUp 0.7s;
}

.skills-tree>ul>li:hover {
  transform: translateY(-8px) scale(1.03);
  box-shadow: 0 8px 32px 0 #00996122, 0 2px 8px 0 rgba(0, 0, 0, 0.07);

}

.skills-tree .skill-root {
  font-weight: 700;
  color: #009961;
  font-size: 1.13em;
  margin-bottom: 0.5em;
  display: block;
  letter-spacing: 0.5px;
  border-bottom: 2px solid #b6f2d5;
  padding-bottom: 4px;
  margin-bottom: 12px;
  text-shadow: 0 1px 0 #e6fff4;
  width: 100%;
  text-align: left;
  background: linear-gradient(90deg, #e6fff4 60%, transparent 100%);
  border-radius: 8px 8px 0 0;
  z-index: 2;
  position: relative;
}

.skills-tree ul ul {
  padding-left: 12px;
  margin-left: 0;
  margin-top: 0;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  height: 100%;
}

.skills-tree ul ul li {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 0.5rem;
  margin-top: 0.5rem;
  position: relative;
  padding-left: 18px;
  transition: color 0.2s;
  animation: fadeIn 0.6s;
  min-height: 32px;
}

.skills-tree ul ul li::before {
  content: '';
  position: absolute;
  left: 6px;
  width: 2px;
  height: 100%;
  background: #009961;
  border-radius: 1px;
  z-index: 0;
  display: block;
}

.skills-tree ul ul li span {
  position: relative;
  transition: color 0.2s;
  flex: 1 1 auto;
  min-width: 0;
  margin-right: 8px;
  white-space: normal;
  overflow: visible;
  text-overflow: unset;
}

.skills-tree ul ul li span::after {
  content: '';
  display: block;
  position: absolute;
  left: 0;
  right: 0;
  bottom: -2px;
  height: 2px;
  border-radius: 2px;
  background: linear-gradient(90deg, #009961 0%, #00c47a 100%);
  opacity: 0;
  transition: opacity 0.5s, box-shadow 0.25s;
  pointer-events: none;
  z-index: 1;
  box-shadow: 0 0 6px 2px #00996155;
}

.skills-tree ul ul li:hover span::after {
  opacity: 1;
  box-shadow: 0 0 12px 4px #00996199;
}

.skills-tree ul ul li:hover span {
  color: #00663d;
}

.skills-tree li::before {
  display: none;
}

.skills-tree span {
  font-weight: 500;
  color: #222;
  letter-spacing: 0.1px;
}

.skills-tree small {
  color: #009961;
  font-size: 0.95em;
  margin-left: 8px;
  background: #e6fff4;
  border-radius: 8px;
  padding: 2px 8px;
  font-weight: 500;
  flex-shrink: 0;
  white-space: nowrap;
}

@media (max-width: 900px) {
  .skills-tree>ul {
    flex-direction: column;
    gap: 24px;
  }

  .skills-tree>ul>li {
    max-width: 100%;
    flex-basis: 100%;
  }
}

@keyframes fadeInUp {
  from {
    opacity: 0;
    transform: translateY(30px);
  }

  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(10px);
  }

  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.page-title .breadcrumbs {
    position: relative;
    z-index: 2;
    display: inline-block;
    margin-top: 0;
    margin-bottom: 1em;
    width: 100%;
}

.page-title .breadcrumbs a,
.page-title .breadcrumbs a:visited {
    background: none;
    border: none;
    color: #009961;
    font-weight: 600;
    padding: 0;
    margin: 0;
    box-shadow: none;
    text-decoration: none;
    cursor: pointer;
    font-size: inherit;
    display: inline;
}
.page-title h1 {
    position: relative;
    z-index: 2;
    display: inline-block;
    font-weight: 700;
    margin-bottom: 0.5em; /* Add space below h1 */
}


.navmenu ul li.active > a,
.navmenu ul li.active > a:visited,
.navmenu ul li.active > .active,
.navmenu ul li.active > .active:visited {
    font-weight: 900;
    font-style: bold;
    color: #009961;
}